html,
body {
	background: black;
	color: #00b800;
	margin: 0;
	padding: 0;
	padding-top: 10px;
	font-family: 'Press Start 2P', cursive;
	text-align: center;
}

body {
	font-weight: 400;
}

.button {
	font-weight: 700;
	font-size: 15px;
	letter-spacing: 1px;
}
button {
	font-family: 'Press Start 2P', cursive;
	display: block;
	margin: 0 auto;
	border: none;
	background: #a80020;
	background: #00b800;
	padding: 10px 20px;
	text-transform: uppercase;
}

button:disabled {
    cursor: not-allowed;
}

h1 {
	font-size: 20px;
	text-align: center;
}

textarea {
	font-family: 'Press Start 2P', cursive;
	border: 3px solid black;
	width: 90%;
	margin: 0 auto;
	outline: none;
	height: 200px;
	background: #00b800;
	padding: 10px;
}

textarea:disabled {
    background-color: #a5a2a2;
}